.Homepage-container {
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Homepage-hotItem-container {
    margin-top: 1rem;
    background-color: rgba(173, 173, 173, 0.11);
    padding: 0.5rem;
    width: 90%;
}

.Homepage-hotItem-title {
    letter-spacing: 0.3rem;
    font-size: 1.5rem;
    margin: 2rem;

}

.Homepage-hotItem {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

