.Products {
    width: 95%;
    display: flex;
    flex-direction: row;
    margin-top: 1rem;
    flex-wrap: wrap;
    justify-content: space-between;
}

.Products-sideBar {
    width: 15%;
    flex-basis: 150px;
}

@media (max-width: 1200px) {
    .Products-sideBar h2 {
        display: none;
    }

    .Products {
        flex-direction: column;
        align-items: center;
    }

    .Products-sideBar {
        margin-top: -1.5rem;
        margin-bottom: 1rem;
        display: flex;
        flex-direction: column;
        align-self: start;
        width: 90%;
        justify-content: center;
        align-items: flex-end;
    }

    .Products-sideBar-selectorContainer {
        display: flex;
    }

    .Products-sideBar-selector {
        margin-left: 1rem;
    }
}

@media (max-width: 992px) {
    .Products-content {
        padding: none;
    }
}

.Products-content {
    background-color: rgba(173, 173, 173, 0.11);
    padding: 1rem;
    width: 85%;
    display: flex;
    flex-direction: column;
}

.Products-content-headers {
    display: flex;
    justify-content: space-between;
}

.Products-content-list {
    margin-top: 1rem;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.Products-pagination {
    align-self: center;
}

.Products-sideBar-selector {
    margin-top: 2rem;
}