.Footer {
    box-sizing: border-box;
    height: 8vh;
    width: 100%;
    box-shadow: 5px 5px 10px 1px rgba(0, 0, 0, 0.5);
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
}

.Footer span {
    margin: 0 1rem;
}

.Footer-icon {
    width: 10%;
    display: flex;
    justify-content: space-evenly;
}