.Navbar {
    width: 100%;
    box-shadow: 5px 5px 5px -5px rgba(0, 0, 0, 0.56);
    box-sizing: border-box;
    margin-bottom: 1rem;
}

.Navbar-title {
    font-size: 2rem;
    font-weight: 500;
}

.Navbar-icon {
    margin-left: auto;
}

@media (max-width: 992px) {
    .Navbar-icon {
        /* display: none; */
        position: relative;
        left: 60px;
        top: 2px
    }
}