.LittleProduct {
    margin: 0 2rem;
    margin-bottom: 2rem;
    text-align: center;
}

.LittleProduct-imgContainer {
    height: 75%;
    display: flex;
    align-items: center;
}

.LittleProduct-contentContainer {
    height: 25%;
}

.LittleProduct-contentContainer p {
    height: 50%;
}

@media (max-width: 600px) {
    .LittleProduct-contentContainer p {
        font-size: 0.8rem;
    }
}